/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Alata", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

[class$=__container] {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 2rem 0;
}

.bg-header {
  background-size: cover;
  background-image: linear-gradient(to bottom, #000 0%, #000 100%);
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
  font-family: "Josefin Sans", sans-serif;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

a {
  text-decoration: none;
}

.header {
  background-repeat: no-repeat;
  background-size: cover;
  padding-bottom: 10rem;
}
@media (min-width: 768px) {
  .header__grid {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.header__heading {
  color: #FFFFFF;
  margin: 10rem 0;
  border: 1px solid #FFFFFF;
  padding: 1rem;
  text-transform: uppercase;
  font-weight: 300;
  font-size: 3.8rem;
  line-height: unset;
}
@media (min-width: 768px) {
  .header__heading {
    width: 60%;
    font-size: 5rem;
    padding: 2rem;
    margin: 5rem 0;
  }
}
@media (min-width: 992px) {
  .header__heading {
    width: 60%;
    font-size: 8rem;
  }
}

.not-avif.not-webp .header {
  background-image: url(../img/mobile/image-hero.jpg);
}

@media (min-width: 768px) {
  .not-avif.not-webp .header {
    background-image: url(../img/desktop/image-hero.jpg);
  }
}
.webp .header {
  background-image: url(../img/mobile/image-hero.webp);
}

@media (min-width: 768px) {
  .webp .header {
    background-image: url(../img/desktop/image-hero.webp);
  }
}
.avif .header {
  background-image: url(../img/mobile/image-hero.webp);
}

@media (min-width: 768px) {
  .avif .header {
    background-image: url(../img/desktop/image-hero.webp);
  }
}
.bar {
  padding: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.bar__nav-active {
  position: relative;
  z-index: 100;
}
@media (min-width: 768px) {
  .bar {
    padding: 5rem 0;
  }
}
.bar__logo {
  margin: 0;
  color: #FFFFFF;
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .bar__logo {
    font-size: 3rem;
  }
}
@media (min-width: 768px) {
  .bar__img {
    display: none;
  }
}
.bar__img--hidden {
  display: none;
}

.nav {
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
  padding-top: 10rem;
}
.nav__active {
  padding-top: 0;
  background-color: black;
  height: 100vh;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0px;
  justify-content: center;
  z-index: 1;
}
@media (min-width: 768px) {
  .nav__active {
    all: unset;
  }
}
.nav__hidden {
  display: none;
}
@media (min-width: 768px) {
  .nav__hidden {
    display: flex;
  }
}
@media (min-width: 768px) {
  .nav {
    flex-direction: row;
    justify-content: space-between;
    gap: 2rem;
    padding: 0;
  }
}
.nav__link {
  color: #FFFFFF;
  font-family: "Josefin Sans", sans-serif;
  font-weight: 300;
  font-size: 2.5rem;
  text-transform: uppercase;
  padding-left: 5rem;
}
@media (min-width: 768px) {
  .nav__link {
    font-size: 1.6rem;
    font-family: "Alata", sans-serif;
    font-weight: 400;
    text-transform: unset;
    padding: 0;
  }
}

.about-us {
  padding: 8rem 1rem 3rem 1rem;
  position: relative;
  max-width: 140rem;
  margin: 0 auto;
}
@media (min-width: 992px) {
  .about-us__image {
    width: 60%;
    max-width: 140rem;
  }
}
@media (min-width: 992px) {
  .about-us__content {
    padding: 8rem;
    width: 40%;
    background-color: #FFFFFF;
    position: absolute;
    right: 17rem;
    top: 26rem;
    max-width: 140rem;
  }
}
.about-us__heading {
  font-family: "Josefin Sans", sans-serif;
  font-weight: 300;
  line-height: unset;
  text-align: center;
  text-transform: uppercase;
  padding-top: 5rem;
}
@media (min-width: 992px) {
  .about-us__heading {
    padding: 0;
  }
}
.about-us__text {
  text-align: center;
  line-height: 1.5;
  font-size: 1.6rem;
  color: hsl(0, 0%, 55%);
}

.creations {
  position: relative;
  max-width: 140rem;
  margin: 0 auto;
}
@media (min-width: 992px) {
  .creations {
    padding: 3rem 0 10rem 0;
  }
}
.creations__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .creations__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 992px) {
  .creations__grid {
    grid-template-columns: repeat(4, 1fr);
  }
}
.creations__heading {
  text-align: center;
  font-family: "Josefin Sans", sans-serif;
  font-weight: 300;
  text-transform: uppercase;
  margin: 3rem 0;
}
@media (min-width: 992px) {
  .creations__heading {
    text-align: start;
  }
}
.creations__btn {
  margin: 3rem auto;
  text-transform: uppercase;
  border: 1px solid #000;
  padding: 1rem 2rem;
  letter-spacing: 5px;
  display: flex;
  justify-content: center;
  width: 60%;
}
@media (min-width: 992px) {
  .creations__btn {
    display: inline-block;
    position: absolute;
    padding: 1rem 3rem;
    right: 7.5rem;
    top: 6.5rem;
    width: auto;
    margin: 0;
  }
}

.card {
  position: relative;
}
.card__image {
  position: relative;
}
@media (min-width: 992px) {
  .card__text {
    position: absolute;
    top: 0;
    width: 100%;
    height: 50.1rem;
    background-color: transparent;
  }
  .card__text:hover {
    background-color: rgba(255, 255, 255, 0.7);
    transition-duration: 0.3s;
  }
  .card__text:hover p {
    color: #000;
  }
}
.card__description {
  position: absolute;
  color: #FFFFFF;
  z-index: 1000;
  bottom: 1rem;
  left: 1rem;
  text-transform: uppercase;
  font-family: "Josefin Sans", sans-serif;
  width: 12rem;
  margin: 0;
}
@media (min-width: 992px) {
  .card__description {
    font-size: 3rem;
    width: 16rem;
    left: 5rem;
  }
}
@media (min-width: 992px) {
  .card__description__span {
    display: block;
  }
}

.footer {
  background-color: #000;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.footer__logo {
  color: #FFFFFF;
  font-size: 3rem;
  text-align: center;
  margin: 0;
  padding: 5rem 0;
  font-weight: bold;
}
@media (min-width: 768px) {
  .footer__logo {
    text-align: left;
    padding: 3rem 0;
  }
}
.footer__copy {
  color: hsl(0, 0%, 41%);
  text-align: center;
  font-size: 1.6rem;
  margin: 0;
  padding-bottom: 3rem;
}
@media (min-width: 768px) {
  .footer__copy {
    text-align: right;
    padding: 0;
  }
}

.footer-nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2.5rem;
}
@media (min-width: 768px) {
  .footer-nav {
    flex-direction: row;
    justify-content: flex-start;
    grid-column: 1/2;
    grid-row: 2/3;
  }
}
.footer-nav__link {
  color: #FFFFFF;
}
@media (min-width: 992px) {
  .footer-nav__link {
    padding-bottom: 1rem;
  }
  .footer-nav__link:hover {
    border-bottom: 2px solid #FFFFFF;
  }
}

.social-media {
  display: flex;
  justify-content: center;
  gap: 2rem;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .social-media {
    justify-content: flex-end;
    grid-column: 2/3;
  }
}
.social-media__img {
  width: 3rem;
  padding-bottom: 1rem;
}
.social-media__img:hover {
  border-bottom: 3px solid #FFFFFF;
  transition-duration: 0.3s;
}

.attribution {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  padding: 1rem 0 3rem 0;
}
@media (min-width: 768px) {
  .attribution {
    flex-direction: row;
    gap: 3rem;
    padding-top: 8rem;
  }
}
.attribution__text {
  margin: 0;
  color: hsl(0, 0%, 55%);
  font-size: 1.6rem;
}
.attribution__link {
  margin-left: 1rem;
  color: #FFFFFF;
}