@use 'variables' as v; 

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
  
body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
}

p {
    color: v.$negro;
    font-size: 2rem;
}
[class$="__container"] {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}
img {
    max-width: 100%;
    width: 100%;
    display: block;
}

ul {
    padding:0;
    list-style: none;
    margin: 0;
}

.seccion {
    padding: 2rem 0;
}
.bg-header {
    background-size: cover;
    background-image: linear-gradient(to bottom, v.$negro 0%, v.$negro 100%);
}